body {
    direction: rtl;
    font-family: "Tajawal-Bold";
    font-size: 1rem;

    @media (max-width: 767.9px) {
        font-size: 0.9rem;
    }
}
.txt-md {
    font-size: 1rem;
}
.txt-sm {
    font-size: 0.9rem;
}
.w-meduim {
    font-family: "Tajawal-Bold";
}
.searchMobile {
    .close-search {
        right: 1rem;
        left: auto;
    }
}
header {
    .menu {
        @media (max-width: 991px) {
            left: -300px;
            right: auto;
            padding: 2rem 1rem;
            transition: left 1s;
        }
        .logo {
            right: 1rem;
            left: auto;
        }
        &.active {
            right: auto;
            left: 0;
        }
    }
}
.icon-close {
    left: 15px;
    right: auto;
}
.home__banner {
    background: url(../images/shape-left-ar.svg) no-repeat right bottom;
    @media (max-width: 991.9px) {
        background: none;
    }

    &:after {
        left: 0;
        right: auto;
        background: url(../images/shape1-cover-ar.png) no-repeat left bottom;
        @media (max-width: 1199.9px) {
            background-size: 100%;
        }
        @media (max-width: 767.9px) {
            background: none;
            display: none;
        }
    }
    .img-banner {
        @media (max-width: 767.9px) {
            background: url(../images/shape1-cover-ar.png) no-repeat left bottom;
            background-size: 100%;
        }
        &:after {
            left: 0;
            right: auto;
            background: url(../images/shapes-cover.png) no-repeat bottom 50px left;
            @media (max-width: 1199.9px) {
                background-size: contain;
            }
            @media (max-width: 767.9px) {
                background: url(../images/shapes-cover.png) no-repeat center;
            }
        }
        &:hover {
            &:after {
                animation: slide1 1s ease-in-out infinite;
                right: 3px;
                left: auto;
            }
        }
    }
    .actions {
        .btn {
            &.btn-register {
                margin-right: 12px;
                margin-left: 0;
            }
        }
    }
}
.about {
    .points {
        &:before {
            right: 7px;
            left: auto;
        }
    }
    .point {
        padding-right: 40px;
        padding-left: 0;
        svg {
            right: 0;
            left: auto;
        }
    }
}
.partners {
    .title {
        &.w-line {
            &:after {
                right: 0;
                left: auto;
            }
        }
    }
}
.m__title {
    margin-bottom: 80px;
}
.title {
    &.custom.w-line {
        &:after {
            right: 0;
            left: auto;
        }
    }
}
.items-customers {
    .slider-actions {
        .lSNext {
            margin-right: 20px;
            margin-left: 0;
        }
    }
    .cover-cust {
        .c {
            &.c1 {
                right: 14%;
                left: auto;
            }
            &.c2 {
                right: 14%;
                left: auto;
            }
            &.c3 {
                right: 39%;
                left: auto;
            }
            &.c4 {
                left: 20%;
                right: auto;
            }
            &.c5 {
                left: 20%;
                right: auto;
            }
        }
    }
}

/*****************Forms**************************/
.form-check {
    margin-right: 30px;
    margin-left: 0;
}
.input-pass {
    a {
        left: 10px;
        right: auto;
    }
}
.cs-hr {
    &:before {
        right: 0;
        left: auto;
    }
}
.btn {
    &.btn-social {
        &.btn-fb {
            margin-left: 40px;
            margin-right: 0;
            @media (max-width: 575.9px) {
                margin-left: 20px;
                margin-right: 0;
            }
        }
    }
    &.btn-google {
        img {
            margin-left: 8px;
            margin-right: 0;
        }
    }
}
.gender-options {
    .option {
        i {
            margin-right: 0;
            margin-left: 20px;
            @media (max-width: 991.9px) {
                margin-right: 0;
                margin-left: 10px;
            }
        }
    }
}
/* ===================================== SignUp =====================================*/
.signup-form {
    .cs-hr {
        &:before {
            right: calc(50% - 120px);
            left: auto;
        }
    }
}
/* ===================================== Information =====================================*/
.dropzone.dz-clickable .dz-message,
.dropzone.dz-clickable .dz-message * {
    text-align: right;
}
.form-check .form-check-input {
    float: right;
    margin-right: -1.5em;
    margin-left: 0;
}
.form-select {
    background-position: left 0.75rem center;
}
.text-start {
    text-align: right !important;
}
/**************list*********************/
.title {
    &.w-line {
        &:after {
            right: 10px;
            left: auto;

            @media (max-width: 767.9px) {
                right: 20px;
                left: auto;
            }
        }
    }
}
.list-categories {
    li {
        &.more-categ {
            svg {
                margin-right: 9px;
                margin-left: 0;
                transform: rotate(180deg);
                animation: none;
            }
        }
    }
}
/***************************Item***************/
.download__banner {
    .actions {
        .btn {
            margin-left: 0.8rem;
            margin-right: 0;
        }
    }
    .cover-banner {
        .layer {
            &.layer1 {
                right: 0;
                left: auto;
                @media (max-width: 1199.9px) {
                    right: -40px;
                    left: auto;
                }
            }
            &.layer2 {
                left: 0;
                right: auto;
                @media (max-width: 1199.9px) {
                    left: -40px;
                    right: auto;
                }
            }
            &.layer3 {
                right: 15px;
                left: auto;
            }
        }
    }
}

.register {
    .form-wrapper {
        right: auto;
        left: 0;

        &:before {
            right: -65px;
            left: auto;
        }
    }
}

/***********************Ring****************/
.coccoc-alo-ph-circle {
    right: -20px;
    left: auto;
}

.coccoc-alo-ph-circle-fill {
    right: 5%;
    left: auto;

    &.fill2 {
        right: 10%;
        left: auto;
    }
    &.fill3 {
        right: 15%;
        left: auto;
    }
}

/******************** End Ring ***************************/

.social-links {
    a {
        margin-left: 8px;

        margin-right: 0;
    }
}
